body {
  background-color: #f8f8f8;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #57abff;
  padding: 1rem;
}

button {
  cursor: pointer;
  background-color: #feb673;
  border: 1px solid #505050;
  border-radius: 5px;
  margin-top: 5px;
  padding: .25rem .5rem;
}

button:hover {
  background-color: #feba3c;
  box-shadow: 10px 8px 5px #787878b2;
}

#filters {
  background-color: #fff;
  border: 1.5px solid #818181a1;
  border-radius: 10px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  width: 90%;
  max-width: 1200px;
  margin: 1rem auto;
  padding: 1rem;
  display: flex;
  box-shadow: 10px 8px 5px #51515148;
}

#filters div {
  flex-direction: column;
  display: flex;
}

#filters label {
  margin-bottom: .5rem;
  font-weight: bold;
}

#filters input, #filters select {
  border: 1px solid #505050;
  border-radius: 5px;
  width: 200px;
  padding: .5rem;
}

#filters button {
  margin-top: 1.5rem;
  padding: .5rem 1rem;
}

#product-list {
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
  margin: 1rem;
  display: grid;
}

.product-card {
  text-align: center;
  background-color: #fff;
  border: 1.5px solid #818181a1;
  border-radius: 10px;
  padding: 1rem;
  overflow: hidden;
  box-shadow: 10px 8px 5px #51515148;
}

.product-card img {
  border-bottom: 1px solid #818181a1;
  width: 100%;
  height: auto;
  margin-bottom: 1rem;
}

.product-card h3 {
  margin: .5rem 0;
  font-size: 1.25rem;
}

.product-card p {
  color: #555;
  margin: .5rem 0;
  font-size: 1rem;
}

.product-card button {
  margin-top: 1rem;
}

.original-price {
  color: gray;
  margin-right: 5px;
  text-decoration: line-through;
}

.discounted-price {
  color: green;
  font-weight: bold;
}

footer {
  text-align: center;
  background-color: #f1f1f1;
  margin-top: 2rem;
  padding: 1rem;
}

@media (width <= 768px) {
  #filters {
    flex-direction: column;
  }

  #product-list {
    grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
  }
}
